import { useEffect, useRef } from 'react';
import { io } from 'socket.io-client';
import useStore from '../store/useStore';

// Para desenvolvimento local, use localhost:3001
// Para produo, substitua pela URL do seu servidor
const SOCKET_URL = process.env.NODE_ENV === 'production' 
  ? 'https://your-socket-server.render.com' 
  : 'http://localhost:3001';

const useSocket = () => {
  const socketRef = useRef(null);
  const { 
    nickname, 
    avatar, 
    addMessage, 
    setConnected,
    isConnected 
  } = useStore();

  useEffect(() => {
    // S conecta se tiver nickname
    if (!nickname) {
      if (socketRef.current) {
        socketRef.current.disconnect();
        socketRef.current = null;
        setConnected(false);
      }
      return;
    }

    // Conectar ao socket
    socketRef.current = io(SOCKET_URL, {
      transports: ['websocket', 'polling'],
      timeout: 20000,
      forceNew: true
    });

    const socket = socketRef.current;

    // Event listeners
    socket.on('connect', () => {
      console.log(' Conectado ao servidor de chat!');
      setConnected(true);
      
      // Enviar dados do usurio
      socket.emit('user_join', {
        nickname,
        avatar
      });
      
      // Mensagem de boas-vindas
      addMessage({
        type: 'system',
        text: ` ${nickname} entrou no chat!`,
        nickname: 'Sistema',
        avatar: ''
      });
    });

    socket.on('disconnect', () => {
      console.log(' Desconectado do servidor');
      setConnected(false);
      
      addMessage({
        type: 'system',
        text: ' Conexo perdida. Tentando reconectar...',
        nickname: 'Sistema',
        avatar: ''
      });
    });

    socket.on('connect_error', (error) => {
      console.error('Erro de conexo:', error);
      setConnected(false);
      
      addMessage({
        type: 'system',
        text: ' Erro de conexo. Modo offline ativado.',
        nickname: 'Sistema',
        avatar: ''
      });
    });

    // Receber mensagens
    socket.on('new_message', (messageData) => {
      // S adicionar se no for a prpria mensagem do usurio
      // (evita duplicao quando o servidor retorna nossa prpria mensagem)
      if (messageData.nickname !== nickname) {
        addMessage({
          type: 'user',
          text: messageData.text,
          nickname: messageData.nickname,
          avatar: messageData.avatar,
          userId: messageData.userId
        });
      }
    });

    // Usurio entrou
    socket.on('user_joined', (userData) => {
      if (userData.nickname !== nickname) {
        addMessage({
          type: 'system',
          text: ` ${userData.nickname} entrou no chat`,
          nickname: 'Sistema',
          avatar: ''
        });
      }
    });

    // Usurio saiu
    socket.on('user_left', (userData) => {
      addMessage({
        type: 'system',
        text: ` ${userData.nickname} saiu do chat`,
        nickname: 'Sistema',
        avatar: ''
      });
    });

    // Contagem de usurios online
    socket.on('users_count', (count) => {
      // Voc pode usar isso para mostrar quantos usurios esto online
      console.log(` Usurios online: ${count}`);
    });

    // Cleanup
    return () => {
      if (socket) {
        socket.disconnect();
      }
    };
  }, [nickname, avatar, addMessage, setConnected]);

  // Funo para enviar mensagem
  const sendMessage = (text) => {
    if (!socketRef.current || !isConnected) {
      // Se offline, apenas adicionar localmente
      addMessage({
        type: 'system',
        text: ' Mensagem no enviada - voc est offline',
        nickname: 'Sistema',
        avatar: '',
        timestamp: Date.now()
      });
      return;
    }

    // Adicionar mensagem localmente primeiro (j que filtramos no new_message)
    addMessage({
      type: 'user',
      text: text,
      nickname: nickname,
      avatar: avatar,
      timestamp: Date.now()
    });

    // Enviar via socket para outros usurios
    socketRef.current.emit('send_message', {
      text,
      nickname,
      avatar
    });
  };

  // Funo para atualizar dados do usurio
  const updateUser = (newNickname, newAvatar) => {
    if (socketRef.current && isConnected) {
      socketRef.current.emit('update_user', {
        nickname: newNickname,
        avatar: newAvatar
      });
    }
  };

  return {
    sendMessage,
    updateUser,
    isConnected,
    socket: socketRef.current
  };
};

export default useSocket;